import { EventEmitter } from "./helpers";
export default class Intro extends EventEmitter {
  constructor(el, data) {
    super();
    this.qData = data;
    this.node = el;

    Array.from(this.node.querySelectorAll(".js-start-quiz")).forEach(element =>
      element.addEventListener("click", this.doStartQuiz.bind(this))
    );
  }

  doStartQuiz(e) {
    console.log("doStartQuiz:", e);
    this.trigger("startQuiz", { a: 11 });
  }
}
